// Generic layout styles

.bg-black {
  background-color: $black;
  color: $white;

  a {
    color: $white;
  }
}

.page-banner-image {
  width: 100%;
  max-width: 1440px;
  position: absolute;
  top: 0;
  height: 25rem;

  @include media-above($break-large) {
    height: 47rem;
  }
}

.site-content {
    padding-top: 25rem;

    @include media-above($break-large) {
      padding-top: 47rem;
    }
}
